@for $key from 0 to 16 {
    //margin
    .m-#{$key} {
        margin: $spacer*$key*.25;
    }

    .mt-#{$key} {
        margin-top: $spacer*$key*.25;
    }

    .mb-#{$key} {
        margin-bottom: $spacer*$key*.25;
    }

    .ms-#{$key} {
        margin-left: $spacer*$key*.25;
    }

    .me-#{$key} {
        margin-right: $spacer*$key*.25;
    }

    .mx-#{$key} {
        margin-left: $spacer*$key*.25;
        margin-right: $spacer*$key*.25;
    }

    .my-#{$key} {
        margin-top: $spacer*$key*.25;
        margin-bottom: $spacer*$key*.25;
    }

    //padding
    .p-#{$key} {
        padding: $spacer*$key*.25;
    }

    .pt-#{$key} {
        padding-top: $spacer*$key*.25;
    }

    .pb-#{$key} {
        padding-bottom: $spacer*$key*.25;
    }

    .ps-#{$key} {
        padding-left: $spacer*$key*.25;
    }

    .pe-#{$key} {
        padding-right: $spacer*$key*.25;
    }

    .px-#{$key} {
        padding-left: $spacer*$key*.25;
        padding-right: $spacer*$key*.25;
    }

    .py-#{$key} {
        padding-top: $spacer*$key*.25;
        padding-bottom: $spacer*$key*.25;
    }
}

@each $breakpoints, $value in reverse($grid-breakpoints) {
    $next: breakpoint-next($breakpoints);
    @if $next != null {
        @include media-breakpoint-down($next) {
            @for $key from 0 to 16 {
                //margin
                .m-#{$breakpoints}-#{$key} {
                    margin: $spacer*$key*.25;
                }
            
                .mt-#{$breakpoints}-#{$key} {
                    margin-top: $spacer*$key*.25;
                }
            
                .mb-#{$breakpoints}-#{$key} {
                    margin-bottom: $spacer*$key*.25;
                }
            
                .ms-#{$breakpoints}-#{$key} {
                    margin-left: $spacer*$key*.25;
                }
            
                .me-#{$breakpoints}-#{$key} {
                    margin-right: $spacer*$key*.25;
                }
            
                .mx-#{$breakpoints}-#{$key} {
                    margin-left: $spacer*$key*.25;
                    margin-right: $spacer*$key*.25;
                }
            
                .my-#{$breakpoints}-#{$key} {
                    margin-top: $spacer*$key*.25;
                    margin-bottom: $spacer*$key*.25;
                }
            
                //padding
                .p-#{$breakpoints}-#{$key} {
                    padding: $spacer*$key*.25;
                }
            
                .pt-#{$breakpoints}-#{$key} {
                    padding-top: $spacer*$key*.25;
                }
            
                .pb-#{$breakpoints}-#{$key} {
                    padding-bottom: $spacer*$key*.25;
                }
            
                .ps-#{$breakpoints}-#{$key} {
                    padding-left: $spacer*$key*.25;
                }
            
                .pe-#{$breakpoints}-#{$key} {
                    padding-right: $spacer*$key*.25;
                }
            
                .px-#{$breakpoints}-#{$key} {
                    padding-left: $spacer*$key*.25;
                    padding-right: $spacer*$key*.25;
                }
            
                .py-#{$breakpoints}-#{$key} {
                    padding-top: $spacer*$key*.25;
                    padding-bottom: $spacer*$key*.25;
                }
            }
        }
    }
}

